*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: black;
}
.wrapper{
  display: flex;
  width: 100%;
  min-height: 100vh;
}
.left-col{
  width: 50%;
  padding: 64px;
}

.left-col h1{
  color: #050505;
  max-width: 629px;
  font-family: Playfair Display;
  font-size: 64px;
  font-style: normal;
  font-weight: 700;
  line-height: 80px; /* 125% */
  padding-top: 55px;
}

.left-col p {
  color: #333;
  max-width: 629px;
  font-family: Work Sans;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px; /* 125% */ 
  padding: 32px 0;
}
.buttons{
  display: flex;
  gap: 32px;
  padding-top: 32px;
  padding-bottom: 64px;
}
.order{
  display: flex;
  padding: 24px 48px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 16px;
  background: #050505;
  border: 1px solid black;
}
.order a {
  color: #FFF;
  font-family: Work Sans;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px; /* 75% */
}

.explore{
  display: flex;
  padding: 24px 48px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 16px;
  border: 1px solid #333;
  background: transparent;
}
.explore a {
  color: #333;
  font-family: Work Sans;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px; /* 75% */
}

.box {
  display: inline-flex;
  padding: 24px;
  align-items: flex-start;
  gap: 80px;
  border-radius: 24px;
  border: 1px solid #111;
}

.box p {
  padding-top: 8px;
  padding-bottom: 10px; 
  color: #111;
  font-family: Work Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}







.right-col{
  width: 50%;
}

.image{
  width: 580px;
  height: 728px;
  flex-shrink: 0;
  border-radius: 51.831px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.image-center{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.big-border{
  position: absolute;
  display: flex;
  margin-top: inherit;
  z-index: -1;
  width: 480px;
  height: 568px;
  flex-shrink: 0;
  border-radius: 48px;
  border: 2px solid #111;
  background: transparent;
  display: none;
}

