@media only screen and (max-width: 768px) {
  .wrapper{
    flex-direction: column;
  }
  .left-col{
    width: 100%;
  }
  .left-col h1 {
    font-size: 52px;
  }
  .right-col{
    width: 100%;
  }
  .buttons{
    flex-direction: column;
  }
  .big-border{
    margin-top: 10rem;
    margin-left: -2rem;
  }

  .box{
    flex-direction: column;
    gap: 30px;
  }

  .image{
      width: 380px;
      height: 528px;
  }

  .order{
    max-width: fit-content;
  }
  .explore{
    max-width: fit-content;
  }

  .box div{
    width: max-content;
  }
}